export default {
  getAboutIsVisible: state => state.aboutIsVisible,
  getContactIsVisible: state => state.contactIsVisible,
  getEducationIsVisible: state => state.educationIsVisible,
  getExperienceIsVisible: state => state.experienceIsVisible,
  getTechnologiesIsVisible: state => state.technologiesIsVisible,
  getWelcomeIsVisible: state => state.welcomeIsVisible,
  activeLink: state => {
    let link = null;

    if (state.welcomeIsVisible && !state.experienceIsVisible) {
      link = "HOME";
    } else if (state.experienceIsVisible && !state.educationIsVisible) {
      link = "EXPERIENCE";
    } else if (state.educationIsVisible && !state.aboutIsVisible) {
      link = "EDUCATION";
    } else if (state.aboutIsVisible && !state.technologiesIsVisible) {
      link = "ABOUT";
    } else if (state.technologiesIsVisible && !state.contactIsVisible) {
      link = "TECHNOLOGIES";
    } else {
      link = "CONTACT";
    }

    return link;
  }
};
