<template>
  <div>
    <transition name="overlay">
      <span v-if="open" class="overlay" @click="$emit('close', $event)"></span>
    </transition>
    <transition name="slide">
      <aside ref="sidenav" v-if="open" class="sidenav">
        <div class="sidenav__content">
          <div class="sidenav__content--header">
            <slot name="sidenav-header" />
          </div>
          <div class="sidenav__content--body">
            <slot name="sidenav-body" />
          </div>
          <div class="sidenav__content--footer">
            <slot name="sidenav-footer" />
          </div>
        </div>
      </aside>
    </transition>
  </div>
</template>

<script>
export default {
  props: {
    open: {
      type: Boolean,
      required: false,
      defaut: false
    }
  },
  data() {
    return {
      offCanvasEl: null
    };
  },
  watch: {
    open(newValue) {
      this.toggleDocumentScroll(newValue);
      this.handleScrollInContent(newValue);
    }
  },
  mounted() {
    this.toggleDocumentScroll(this.open);
  },
  methods: {
    toggleDocumentScroll(value) {
      if (value) {
        document.documentElement.classList.add("no-scroll");
      } else {
        document.documentElement.classList.remove("no-scroll");
      }
    },
    handleScrollInContent(active) {
      // time out because of the transition of the element
      setTimeout(() => {
        if (active) {
          this.offCanvasEl = this.$refs["sidenav"] || this.offCanvasEl;
        }

        if (this.offCanvasEl) {
          const addOrRemove = active
            ? "addEventListener"
            : "removeEventListener";
          this.offCanvasEl[addOrRemove]("scroll", this.handleScroll);
        }
      }, 0);
    },
    handleScroll() {
      if (this.offCanvasEl) {
        this.$emit("scroll", this.offCanvasEl.scrollTop);
      }
    }
  }
};
</script>

