export default {
  setSectionVisible({ commit }, payload) {
    switch (payload.section) {
      case "about":
        commit("setAboutIsVisible", payload.visibility);
        break;
      case "contact":
        commit("setContactIsVisible", payload.visibility);
        break;
      case "education":
        commit("setEducationIsVisible", payload.visibility);
        break;
      case "experience":
        commit("setExperienceIsVisible", payload.visibility);
        break;
      case "technologies":
        commit("setTechnologiesIsVisible", payload.visibility);
        break;
      case "welcome":
        commit("setWelcomeIsVisible", payload.visibility);
        break;

      default:
        break;
    }
  }
};
