<script>
import Sidenav from "./Sidenav.vue";

export default {
  components: {
    Sidenav
  },
  data() {
    return {
      myStyle: {},
      elOffset: 0,
      sidenavOpen: false
    };
  },
  created() {
    window.addEventListener("scroll", this.onScroll);
  },
  methods: {
    onScroll(e) {
      this.elOffset = e.srcElement.defaultView.pageYOffset;
    },
    openSidenav(e) {
      e.target.closest("button").firstChild.classList.toggle("open");
      this.sidenavOpen = !this.sidenavOpen;
    },
    dismissSidenav() {
      this.sidenavOpen = false;
    },
    scrollToSection(position) {
      window.scrollTo({
        top: position + 10,
        behavior: "smooth"
      });
    }
  },
  computed: {
    activeLink() {
      return this.$store.getters.activeLink;
    }
  }
};
</script>

